<template>
  <div class="item-wrapper">
 <div class="item">
     总数：{{totalNum}}
 </div>
  <div class="item">
     总价：{{totalPrice}}
 </div>
  <div @click="clearAllCart" class="item btn btn-danger">清空购物车</div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: "info",
  data() {
    return {}
  },
    computed:{
    // 映射数据到页面,把事件映射到store的actions里面
      ...mapGetters(['totalNum','totalPrice'])
},
  methods:{
    // 映射事件
    ...mapActions(["clearAllCart"])
  }
}
</script>

<style scoped>
    .item-wrapper {
        display: flex;
        background-color: #dfdfdf;
        align-items: center;
        justify-content: center;
    }
    
    .item {
        flex: 1;
    }
</style>


