// store 对象

// 商品列表
const state = {
  shoplist: [{
    id: 1,
    name: '汽车',
    price: '666'
  }, {
    id: 2,
    name: '飞机',
    price: '777'
  }, {
    id: 3,
    name: '轮船',
    price: '888'
  }, {
    id: 4,
    name: '火箭',
    price: '999'
  }],

  // 已选择的商品
  added: []
}

// 向外输出数据
const getters = {
  // 商品列表
  shoplist: state => state.shoplist,

  // 购物车列表
  cartProducts: state => {
    return state.added.map(({id,num}) => {
      let _n = state.shoplist.find(n => n.id === id);
      return {
        ..._n,
        num
      }
    })
  },

//   计算总数
  totalNum:(state,getters)=>{
      let _total = 0;

      getters.cartProducts.forEach(n =>{
          _total += n.num
      })
      return _total
  },

//   计算总价
totalPrice:(state,getters)=>{
    let _total = 0;

    getters.cartProducts.forEach(n =>{
        _total += n.num*n.price
    })
    return _total
}

}

const actions = {
  addToCart({commit}, n) {
    commit('add', {
      id: n.id
    })
  },

//   删除商品
delGoods({commit},n){
    commit('delFn',n)
},

// 清空购物车
clearAllCart({commit}){
    commit('clearAll')
}
}

const mutations = {
  add(state, {id}) {
    // Console.log(id)
    let _n = state.added.find(n => n.id == id);
    if (_n) {
      // 商品存在
      _n.num++;
    } else {
      // 商品不存在
      state.added.push({
        id,
        num: 1
      })
    }
  },

//   删除商品
  delFn(state,_goods){
      state.added.forEach((n,i)=>{
          if(n.id == _goods.id){
              state.added.splice(i,1);
          }
      })
  },

//   清空购物车
clearAll(state){
    state.added = []
}
}

export default {
  state,
  getters,
  mutations,
  actions
}



